.element {
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 10px;
    margin: 10px;
    background-color: #f9f9f9;
    transition: background-color 0.3s ease;
}

.element:hover {
    background-color: #e0e0e0;
}

.element.selected {
    border-color: #007bff;
    box-shadow: 0 0 10px rgba(0, 123, 255, 0.5);
}

.element img {
    max-width: 100%;
    height: auto;
    display: block;
}

.element.text {
    font-size: 16px;
    color: #333;
}

.element.button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    border-radius: 4px;
}

.element.button:hover {
    background-color: #0056b3;
}