.canvas {
    width: 100%;
    height: 100vh;
    background-color: #f9f9f9;
    border: 2px dashed #ccc;
    position: relative;
    overflow: hidden;
}

.element {
    position: absolute;
    cursor: move;
    transition: transform 0.2s ease;
}

.element:hover {
    opacity: 0.8;
}

.placeholder {
    background-color: rgba(0, 123, 255, 0.2);
    border: 2px dashed rgba(0, 123, 255, 0.5);
    position: absolute;
    z-index: 1;
}